.switch {
  width: 40px;
  height: 20px;
  margin-right: 14px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-image: linear-gradient(to bottom, var(--violet), var(--white) );
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 2px;
}

input:checked + .slider {
  background-image: linear-gradient(#4f2ee8, #686868);
}

input:checked + .slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(18px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.mobile-menu {
  color: #000;
